export default {
	data() {
		return {
			getimg:this.$getimg,
			list:[]
		};
	},
	onLoad(){
		this._load()
	},
	methods:{
		_load(){
			this.$api.http.get('discount/get_discount_goods').then(res=>{
				this.list=res.data					
			})
		},
		jump_pro(id){
			uni.navigateTo({
				url: '/pages/extend-view/productDetail/productDetail?id='+id
			});
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
